import Constant from '../Constant';
// import axios from 'axios';
import { createApi } from "unsplash-js";
import Axios from 'axios';

const imageApi = createApi({
  // Don't forget to set your access token here!
  // See https://unsplash.com/developers
  accessKey: "UZc-Cn14G47aaw0SGFkR_16DLMHNBZfZTT_BClvvtQ8"
});

const serviceKey_weather = 'f1uIoA9bs5dByU%2Bb5Gft7%2B0UF9deIDlz1lZe1euBRjG0vFNAkpdFU375AzWBvuJpNC0bDDXph34QKEJphrVDEQ%3D%3D';

export default {
  [Constant.FETCH_IMAGE_SLIDE] : (store) => {
    imageApi.search
    .getPhotos({ query: "Penguin", orientation: "landscape" })
    .then(result => {
      const imagedata = result.response;
      console.log(imagedata);
      store.commit(Constant.FETCH_EVENTSLIDE, { bannerdata : imagedata })
    })
    .catch(() => {
      console.log("something went wrong!");
    });
  },
  [Constant.FETCH_IMG_GRID] : (store) => {
    imageApi.search
    .getPhotos({ query: "seoul", orientation: "landscape" })
    .then(result => {
      const clipdata = result.response;
      console.log(clipdata);
      store.commit(Constant.FETCH_IMG_GRID, { clipdata : clipdata })
    })
    .catch(() => {
      console.log("something went wrong!");
    });
  },
  [Constant.FETCH_WEATHER] : () => {
    Axios.defaults.baseURL = 'http://apis.data.go.kr'; //서버주소
    Axios.defaults.headers.post['Content-Type'] = 'application/json;charset=utf-8';
    Axios.defaults.headers.post['Acess-Control-Allow-Origin'] = '*';
    Axios({
      method: 'get',
      url: `/1360000/AsosDalyInfoService/getWthrDataList?serviceKey=${serviceKey_weather}&numOfRows=10&pageNo=1&dataCd=ASOS&dateCd=DAY&startDt=20100101&endDt=20100102&stnIds=108`,
    })
    .then(res => {
      console.info(res);
      return res;
    })
    .catch(console.error)
  }

}